*
{
  padding:0px;
  margin: 0px;
  box-sizing: border-box;
}
@font-face {
  font-family: 'SamsungOne 400';
  font-style: normal;
  font-weight: normal;
  src: local('SamsungOne 400'), url('D:\My Project\office\src\Component\Font\SamsungOne-400.woff') format('woff');
  }
  

  @font-face {
  font-family: 'SamsungOne 700';
  font-style: normal;
  font-weight: normal;
  src: local('SamsungOne 700'), url('D:\My Project\office\src\Component\Font\SamsungOne-700.woff') format('woff');
  }
  

  @font-face {
  font-family: 'Samsung Sharp Sans Bold';
  font-style: normal;
  font-weight: normal;
  src: local('Samsung Sharp Sans Bold'), url('D:\My Project\office\src\Component\Font\SamsungSharpSans-Bold.woff') format('woff');
  }
a,p
{
  text-decoration: none;
  font-family: 'SamsungOne 400';
}
body
{
  font-family: 'Samsung Sharp Sans Bold';
  background-color: #faf1e6 ;
  
}
/* Navbar CSS */
.navbar-nav
{
  background-color:transparent;
  text-align: center;
  position: fixed;
  top:0px;
  width: 100%;
  overflow: hidden;
  padding-top:10px;
  z-index: 1;
}
.navbar-item
{
  display: inline-block;
  padding: 10px 30px;
  transition: .3s linear;
}
.navbar-item a
{
  color: black;
  letter-spacing: 5px;
  font-weight: lighter;
  font-size: 12px;
}
.color-mode
{
  position: absolute;
  right:20px;
  top:20px;
  font-size: 20px;
  display: none;
}
.nav-logo
{
  position: absolute;
  top:20px;
  left:20px;
  color: black;
}


/* Home's CSS */
#home
{
  background-color: transparent;
  width:100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-c h1
{
  letter-spacing: 10px;
  animation: color 10s linear infinite;
  font-weight: bold;
  background: linear-gradient(90deg, #4b778d, #28b5b5, #8fd9a8, #d2e69c,#4b778d);
  background-size: 400%;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  font-size: 5vw;
}
.bg
{
  content: "";
  width:500px;
  height: 500px;
  background: linear-gradient(90deg, #4b778d, #28b5b5, #8fd9a8, #d2e69c);
  position: absolute;
  border-radius: 50%;
  top:-300px;
  left:-300px;
  
}



.home-c
{
  margin-top: 100px;
}
.home-c a
{
  background: linear-gradient(90deg, #4b778d, #28b5b5, #8fd9a8, #d2e69c,#4b778d);
  padding: 10px 60px;
  background-size: 200%;
  color: white;
}
.home-c a:hover
{
  animation: color 5s linear infinite;
}


/* What I Do's CSS */
#myservice
{
  background-color:#8fd9a8;
  padding: 20px;
  position: relative;
  overflow: hidden;
  margin-top: 100px;
}

.ms-img1,.ms-img2,.ms-img3,.ms-img4
{
  width:100px;
  height: 100px;
  text-align: center;
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.ms-img1::before,.ms-img2::before,.ms-img3::before,.ms-img4::before
{
  content: '';
  width:100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
  z-index: -1;
  transition: .5s ease-in-out;
}
.ms-img1::before
{
  background-color: #f54748;
}
.ms-img2::before
{
  background-color:#ffc83c; 
}
.ms-img3::before
{
  background-color: #e9896a;
}
.ms-img4::before
{
  background-color: #ff96ad;
}

.ms-t:hover .ms-img1::before
{
  transform: scale(6);
}
.ms-t:hover .ms-img2::before
{
  transform: scale(6);
}
.ms-t:hover .ms-img3::before
{
  transform: scale(6);
}
.ms-t:hover .ms-img4::before
{
  transform: scale(6);
}
.ms-t
{
  background-color: #faf1e6;
  padding: 20px;
  border-radius: 20px;
  margin: 10px;
  text-align: center;
  overflow: hidden;
  z-index: 1;
  position: relative;
}
.ms-c .col-lg-3
{
 z-index: 5;
}
.mys-bg
{
  content: "";
  background-color: #28b5b5;
  position: absolute;
  top:0px;
  right: 0px;
  width:100%;
  height: 100%;
  clip-path: polygon(100% 0, 100% 0%, 100% 100%, 0% 100%);
  
}

/* About us'CSS */

.ab-all-c
{
  width:100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
}
.ab-title
{
  text-align: center;
  margin: 80px;
}
.ab-c
{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.ab-img
{
  width:50%;
  height: auto;
  position: relative;
  left: 100px;
}
.ab-content
{
  width:50%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 10vw;
}
.ab-img img
{ 
  width: 100%;
  height: 100%;
}
.ab-content
{
  background-color: #e9896a;
}
.partner-title
{
  text-align: center;
  margin: 50px;
}


#section1
{
  width:100%;
  height: 400px;
  position: relative;
  margin-top: 100px;
}

#section1 .container-fluid
{
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
}
#section1 h1
{
  font-size: 10vw;
  background: url("D:\My Project\office\src\Component\Images\4165376.jpg") no-repeat;
  background-size: 200%;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  animation: bg 5s linear infinite;
}
.sec1-c .btn
{
  border-radius: 0px;
  padding: 10px 60px;
}
#section2
{
  padding: 50px;
  position: relative;
  overflow: hidden;
}
.sec2-title,.sec3-title
{
  margin: 50px;
  text-align: center;
}
.card
{
  width: 350px;
  margin: 10px;
}
.sec2-bg
{
  content: '';
  background-color: #e9896a;
  width:1000px;
  height: 100%;
  position: absolute;
  top:0px;
  left:0%;
  clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
  z-index: -1;
}

#section3
{
  position: relative;
  padding: 20px;
}
.carousel-c
{
  background-color: white;
  padding: 30px;
  margin: 10px;
}
.carousel-indicators li
{
  position: relative;
  top:100px;
  width:10px;
  height: 10px;
  border-radius: 50px;
  margin: 10px;
  background-color:#e9896a;
  cursor: pointer;
}
.sec3-bg
{
  content: '';
  background-color: #8fd9a8;
  width:1000px;
  height: 100%;
  position: absolute;
  top:0px;
  right:0%;
  clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
  z-index: -1;
}

#section4
{
  position: relative;
}

.sec4-c
{
  display: grid;
  grid-template-columns: auto auto auto auto;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.sec4-bg
{
  background-color: #8fd9a8;
width:300px;
height: 300px;
position: relative;
 
}
.sec4-title
{
  text-align: center;
  margin: 100px;
}

.sec4-bg:nth-child(1)
{
  background:url("https://image.freepik.com/free-photo/portrait-young-woman-with-vr-glasses_23-2148932817.jpg")no-repeat;
  background-size: cover;
  background-position: center;
  grid-column: 1 / 3;
  grid-row: 1 / 3;
  width:600px;
  height: 600px;
}
.sec4-bg:nth-child(2)
{
  background: url("https://image.freepik.com/free-photo/discussing-logo_1098-18299.jpg") no-repeat;
  background-size: cover;
}
.sec4-bg:nth-child(3)
{
  background: url("https://image.freepik.com/free-psd/friends-with-hoddies-showing-mobiles_23-2148473809.jpg")no-repeat;
  background-size: cover;
  background-position: center;
}
.sec4-bg:nth-child(4)
{
  background:url("https://image.freepik.com/free-photo/group-people-working-out-business-plan-office_1303-15874.jpg") no-repeat;
  background-size: cover;
}
.sec4-bg:nth-child(5)
{
  background: url("https://image.freepik.com/free-photo/businessman-holding-hologram-microchip-smart-technology_53876-96308.jpg") no-repeat;
  background-size: cover;
}
.sec4-text-c
{
  background:linear-gradient(to bottom,rgba(0, 0, 0, 0.123),black);
  top:0px;
  width:100%;
  height: 0%;
  color: white;
  overflow: hidden;
  transition: .5s linear;
  position: absolute;
  
}
.sec4-bg:hover .sec4-text-c
{ 
  height:100%;
}
.sec4-t
{
 width:100%;
 bottom: 0px;
 padding: 20px;
 position: absolute;
text-overflow: inherit;
  
}
footer
{
  width:100%;
  height: auto;
  margin-top: 100px;
  padding: 30px;
}
.f-c
{
  display: flex;
  justify-content: space-around;
}
.links,.follow
{
  color: white;
  line-height: 20px;
}
.aboutus
{
  width:400px;
  color: white;
}
.aboutus p
{
  font-size: 14px;
}
.links a 
{
  color: #faf1e6;
  font-size: 14px;
}
.follow a
{
  color: #faf1e6;
  font-size: 20px;
  margin: 10px;
}

@keyframes bg 
{
 0%
 {
   background-position: top;
 }
 50%
 {
   background-position: bottom;
 }
}


@keyframes color
{
  0%
  {
    background-position: 0%;
  }
  100%
  {
    background-position: 400%;
  }
}

@media screen and (max-width:950px)
{
.ab-all-c
{
  padding: 10px;
}
.ab-c
{
  flex-direction: column;
}
.ab-content
{
  width: 100%;
  padding: 20px;
  height: auto;
}
.ab-img
{
  width:100%;
  height: 200px;
  left: 0px;
}
}


@media screen and (max-width:1050px)
{
  .sec4-bg:nth-child(1)
  {
    width:400px;
    height: 400px;
  }
  .sec4-bg
  {
    width:200px;
    height: 200px;
  }
}

@media screen and (max-width:850px)
{
  .navbar-nav
  {
    padding-top: 60px;
  }
  .navbar-item
  {
    display: block;
    margin-top: 20px;
  }
  .navbar-list ul
  {
    margin-top: 50px;
  }
  .color-mode
  {
    display: block;
  }
  #section2
  {
    padding: 10px;
  }
  .sec4-c
  {
    grid-template-columns: auto auto;
  }
  .sec3-bg
  {
    width: 100%;
  }
  .sec4-bg:nth-child(1)
  {
    height: 300px;
    grid-column: 1/3;
  }
  .aboutus
  {
    width: 100%;
  }
  .f-c
  {
    flex-direction: column;
  }
  
}

@media screen and (max-width:650px)
{
  .sec4-c
  {
    grid-template-columns: auto;
  }
 .sec4-bg
 {
   
   height: 300px;
   width: 100%;
  
 }
 .sec4-bg:nth-child(1)
 {
   grid-column: 1/1;
   grid-row: 1/0;
   width: 300px;
   height: 300px;

 }
 #section4
 {
   padding: 10px;
 }
}